<template>
<div class="cate">
       <van-search shape="round"  placeholder="请输入搜索关键词" />


   <van-tabs scrollspy  class="cate">
      <van-tab class="content" v-for="i in cateList" :key="i.id" :title="i.cate_name">
          <h3>{{i.cate_name}}</h3>

         
          <ul>
              <router-link tag="li" v-for="child in i.children"  :key="child.id"
               :to="{
                   path:`/goodlist/${child.id}`,
              query:{title:child.cate_name}
              }"
              
              >
                        <van-image :src="child.pic" alt=""/>
                        <span>{{child.cate_name}}</span>
           
              </router-link>
          </ul>
          <div class="null"></div>
    </van-tab>
</van-tabs>
</div>
</template>

<script>
export default {
    name:"Category",
    props:["id"],
    data(){
        return {
            cateList:[]
        }
    },
    created(){
        this.$axios.get('api/category').then(d=>{
            this.cateList=d.data.data;
           
            
        })
    }
}
</script>
<style lang="less" >
// .cate{
