// }


.cate{
 .van-tabs__wrap{
        height: auto;
        position: fixed;
        left: 0px;
        z-index: 999;
        

    }

    .van-tabs__nav{
       display: block;
        width: 97px;

       .van-tab{
           line-height: 50px;
           font-size: 16px;
       }
       .van-tab--active{
           border-left: 2px solid #fc4141;
           color: red;
       }
       
    }
    .van-tabs__content{
        padding-left: 110px;
        .content{
            line-height: 20px;
            h3{
                text-align: center;
            }
            ul{
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                
                .van-image__img{
                    display: block;
                  width: 98%;
                    height: 100%;

                }
                li{
                    flex: 33%;
                    text-align: center;
                    // height: 180px;
                   
                  span{
                      text-align: center;
                    //   font-size: 1rem;
                  }

                }
            }
        }
    }
    .van-tabs__line{
        background-color: transparent;
    }

   
}
.null{
    height: 60px;
}
